}
@font-face {
    font-family: 'anaphora';
    src: url('../../fonts/Anaphora-Light-trial.ttf');
    font-weight: 400;
    font-weight: normal;
}
.wait-and-fade{
    animation: wait-and-fade 5s steps(8) forwards;    
}

@keyframes wait-and-fade {
    0% {
        opacity: 1;
    }
    1%{opacity: 1;}
    70%{opacity: 1;}
    100% {
        opacity: 0;
    }
}
.molly{
