@keyframes rotate-90 {

  0% {
    transform: rotate(0deg);
    opacity: 0;

  }

  10% {
    transform: rotate(0deg);
    opacity: 1;

  }

  80% {
    transform: rotate(90deg);
    opacity: 1;
  }

  100% {
    transform: rotate(90deg);
    opacity: 0;

  }
}

