/* Circle */
.circle {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  /* background-color: #ff4343; */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.circle:before,
.circle:after {
  content: '';
  display: block;
  position: absolute;
  top: 0; 
  right: 0; 
  bottom: 0; 
  left: 0;
  border: 1px solid rgb(207,181,59);
  border-radius: 50%;
